import React, { Component } from 'react'
import { Input, Upload, Icon, message, Button  } from 'antd';
import { getBase64, beforeUpload } from '../../../../utils/upload'
import { createArticle, getArticleDetail, updateArticleDetail } from '../../../../api/api'
import { getQueryString } from '../../../../utils/common'
import './index.less'
import E from 'wangeditor'
import axios from '../../../../utils/request';
const { TextArea } = Input;

export default class ArticleEdit extends Component {
  componentDidMount() {
    // console.log(this.props.history.location.search)
    if (getQueryString('id')) {
      this.init()
    }
    this.initEditor()
  }
  state = {
    isFirst: true,
    loading: false,
    title: '',
    imageUrl: '',
    intro: '',
    content: '',
    state: ''
  };
  init = () => {
    let info = {
      id: getQueryString('id')
    }
    axios.post(getArticleDetail, info).then((res) => {
      if (res.data.errno === 0) {
        let detail = res.data.data
        this.setState(() => {
          return {
            title: detail.title,
            imageUrl: detail.article_img,
            intro: detail.list_show_text,
            content: detail.content,
            state: detail.state
          }
        })
      }
    })
  }
  initEditor = () => {
    const elem = this.refs.editorElem
    const editor = new E(elem)
 
    this.editor = editor
 
    editor.customConfig.zIndex = 100
    editor.customConfig.uploadImgServer ='https://www.mocky.io/v2/5cc8019d300000980a055e76'
    //  1 
    editor.customConfig.uploadImgMaxLength = 1
    editor.customConfig.customUploadImg = function (files, insert) {
      // files  input 
      console.log(files)
      if (files[0]) {
        const formData = new window.FormData()
        formData.append('file', files[0], 'cover.jpg')
        fetch('https://www.mocky.io/v2/5cc8019d300000980a055e76', {
          method: 'POST',
          body: formData
        }).then((res) => {
          return res.json()
        }).then((res) => {
          const data = res.resultData
          if (data) {
            // 
            insert(data.resourceUrl)
          } else {
            // console.log(data.msg)
          }
        })
      } else {
        message.info('')
      }
    }
    editor.customConfig.menus = [
      'head', // 
      'bold', // 
      'fontSize', // 
      'fontName', // 
      'italic', // 
      'underline', // 
      'strikeThrough', // 
      'foreColor', // 
      'backColor', // 
      'link', // 
      'list', // 
      'justify', // 
      'quote', // 
      'emoticon', // 
      'image', // 
      'table', // 
      'video', // 
      // 'code', // 
      'undo', // 
      'redo' // 
    ]
    editor.customConfig.lang = {
      '': 'Title',
      '': 'Size',
      '': 'Color',
      '': 'List',
      '': '',
      '': '',
      '': 'Align',
      '': '',
      '': '',
      '': '',
      '': 'p',
      '': 'link text',
      '': 'link',
      '': 'Upload',
      '': 'Web',
      'link': 'image url',
      '': 'Video',
      '': 'format',
      '': 'Upload',
      '': 'init'
    }
    editor.create()
  }
  componentDidUpdate() {
    if (this.state.isFirst && getQueryString('id')) {
      this.setState(() => {
        return {
          isFirst: false
        }
      }, () => {
        this.editor.txt.html(this.state.content)
      })
    }
    // this.editor.txt.html(this.state.content)
  }
  handleChange = info => {
    if (info.file.status === 'uploading') {
      this.setState({ loading: true });
      return;
    }
    if (info.file.status === 'done') {
      // Get this url from response in real world.
      getBase64(info.file.originFileObj, imageUrl =>
        this.setState(() => {
          return {
            imageUrl,
            loading: false,
          }
        })
      );
    }
  };
  changeTitle = ( {target: {value}} ) => {
    this.setState(() => {
      return {
        title: value
      }
    })
  }
  changeTextArea = ( {target: {value}} ) => {
    this.setState(() => {
      return {
        intro: value
      }
    })
  }
  createArticle = () => {
    let { title, imageUrl, intro, state } = this.state
    let info = {
      title,
      imageUrl,
      intro,
      content: this.editor.txt.html()
    }
    // console.log(info)
    // console.log(this.editor.txt.html())
    if (!getQueryString('id')) {
      axios.post(createArticle, info).then((res) => {
        if (res.data.errno === 0) {
          message.success('...')
          setTimeout(() => {
            this.props.history.replace('/home/article')
          }, 1500)
        }
      })
    } else {
      if (state === 2) {
        let info = {
          id: getQueryString('id'),
          title,
          imageUrl,
          intro,
          content: this.editor.txt.html()
        }
        axios.post(updateArticleDetail, info).then((res) => {
          if (res.data.errno === 0) {
            message.success('...')
            setTimeout(() => {
              this.props.history.replace('/home/article')
            }, 1500)
          }
        })
      } else {
        message.error('')
      }
    }
  }
  render() {
    const uploadButton = (
      <div>
        <Icon type={this.state.loading ? 'loading' : 'plus'} />
        <div className="ant-upload-text">Upload(400  250)</div>
      </div>
    );
    const { imageUrl, title, intro } = this.state;
    return (
      <div className="articleEdit">
        <Input className="common-input" value={title} onChange={this.changeTitle} placeholder="" />
        <div className="mid-wrapper">
          <Upload
            name="avatar"
            listType="picture-card"
            className="avatar-uploader"
            showUploadList={false}
            action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
            beforeUpload={beforeUpload}
            onChange={this.handleChange}
          >
            {imageUrl ? <img src={imageUrl} alt="avatar" style={{ width: '100%' }} /> : uploadButton}
          </Upload>
          <TextArea
            onChange={this.changeTextArea}
            className="textarea-input"
            autosize={false}
            placeholder=""
            value={intro}
          />
        </div>
        <div className="editor" ref='editorElem' style={{ textAlign: 'left' }} />
        <Button type="primary" className="confirm-btn" onClick={this.createArticle}></Button>
      </div>
    )
  }
}
